@import url('https://fonts.googleapis.com/css?family=Comfortaa');
@import '../../../modules/mediaQuery/app';


.outbox {
  min-width: max-content;
  min-width: 386px;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  background: rgb(244, 244, 244);
  font-size: 1rem; }

.box {
  width: 100%;
  margin-top: 12vh;
  display: flex;
  justify-content: center;
  align-items: center; }


.content {
  padding: 3%;
  margin: 4em;
  background: white;
  min-width: max-content;
  justify-content: center;
  align-items: center; }


.title {
  color: blue;
  font-size: 2rem;
  font-family: 'Comfortaa', cursive; }


.table {
  width: max-content; }

.process_box {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-end; }

.total {
  font-size: 20px;
  font-weight: bold; }



